	export default {
		props: {
			avatar: String,
			text: String,
			isShowArrow: {type: Boolean, default: true},
			index: Number,
			subAvatar: String
		},
		name: '',
		data () {
			return {

			}
		},
		onLoad () {

		},
		methods: {
			onSwitchToEvent (text, index) {
				this.$emit('onSwitchToEvent', {text, index})
			},
			onPreviewAvatar () {
				let url = '';
				url = this.avatar || this.subAvatar;
